import { Component, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatToolbarModule } from '@angular/material/toolbar';

import { MatDialogModule, MatDialog } from '@angular/material/dialog';
import { MatDialogConfig } from '@angular/material/dialog';

import { JavaDialogComponent } from './java-dialog/java-dialog.component';
import { PythonDialogComponent } from './python-dialog/python-dialog.component';
import { AngularDialogComponent } from './angular-dialog/angular-dialog.component';
import { SpringbootDialogComponent } from './springboot-dialog/springboot-dialog.component';
import { SqlDialogComponent } from './sql-dialog/sql-dialog.component';
import { PlsqlDialogComponent } from './plsql-dialog/plsql-dialog.component';
import { CDialogComponent } from './c-dialog/c-dialog.component';
import { CsDialogComponent } from './cs-dialog/cs-dialog.component';
import { IonicDialogComponent } from './ionic-dialog/ionic-dialog.component';
import { AgileDialogComponent } from './agile-dialog/agile-dialog.component';
import { CommonModule } from '@angular/common';

import { register as registerSwiperElements } from 'swiper/element/bundle';

@Component({
  selector: 'app-root',
  standalone: true,
  imports: [RouterOutlet,MatIconModule,MatToolbarModule,
    MatDialogModule,MatButtonModule,CommonModule],
  templateUrl: './app.component.html',
  styleUrl: './app.component.css',
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppComponent {

  darkMode: boolean = false;
  constructor(public dialog: MatDialog){
  }
  ngOnInit(): void {
    registerSwiperElements();
  }
  switchMode(){
    this.darkMode = !this.darkMode;
  };
  openJavaDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(JavaDialogComponent, config);
  }
  openPythonDialog(panel?: string)
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  if (panel) {
    config.data = { panelToOpen: panel };
  }
  this.dialog.open(PythonDialogComponent, config);
  }
  openCDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(CDialogComponent, config);
  }
  openCsDialog(panel?: string)
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  if (panel) {
    config.data = { panelToOpen: panel };
  }
  this.dialog.open(CsDialogComponent, config);
  }
  openSqlDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(SqlDialogComponent, config);
  }
  openPlsqlDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(PlsqlDialogComponent, config);
  }
  openAngularDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(AngularDialogComponent, config);
  }
  openIonicDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(IonicDialogComponent, config);
  }
  openSpringbootDialog(panel?: string)
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  if (panel) {
    config.data = { panelToOpen: panel };
  }
  this.dialog.open(SpringbootDialogComponent, config);
  }
  openAgileDialog()
  {
  let config = new MatDialogConfig();
  config.width = "90vw";
  config.maxWidth = "none";
  config.height = "90vh";
  config.maxHeight = "none";
  config.data = "";
  this.dialog.open(AgileDialogComponent, config);
  }
}
