import { AfterViewInit, Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { MatDialogModule } from '@angular/material/dialog';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatExpansionModule,MatExpansionPanel  } from '@angular/material/expansion';


@Component({
  selector: 'app-python-dialog',
  standalone: true,
  imports: [MatDialogModule,MatExpansionModule],
  templateUrl: './python-dialog.component.html',
  styleUrl: './python-dialog.component.css'
})
export class PythonDialogComponent {
  constructor(@Inject(MAT_DIALOG_DATA) public data: any) {}
  @ViewChild('tensorflow') tensorflowPanel!: MatExpansionPanel;
  @ViewChild('tensorflowPanelRef', { read: ElementRef }) tensorflowPanelRef!: ElementRef;

  ngAfterViewInit() {
    if (this.data?.panelToOpen === 'tensorflow') {
      setTimeout(() => {
        this.tensorflowPanel.open();
        setTimeout(() => {
          this.tensorflowPanelRef.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }, 100);
      }, 100);
    }
  }
}
