import { AfterViewInit, Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { MatDialogModule } from '@angular/material/dialog';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatExpansionModule,MatExpansionPanel  } from '@angular/material/expansion';

@Component({
  selector: 'app-springboot-dialog',
  standalone: true,
  imports: [MatDialogModule,MatExpansionModule],
  templateUrl: './springboot-dialog.component.html',
  styleUrl: './springboot-dialog.component.css'
})
export class SpringbootDialogComponent {
  constructor(@Inject(MAT_DIALOG_DATA) public data: any) {}
  @ViewChild('session') sessionPanel!: MatExpansionPanel;
  @ViewChild('sessionPanelRef', { read: ElementRef }) sessionPanelRef!: ElementRef;
  ngAfterViewInit() {
    if (this.data?.panelToOpen === 'session') {
      setTimeout(() => {
        this.sessionPanel.open();
        setTimeout(() => {
          this.sessionPanelRef.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }, 100);
      }, 100);
    }
  }
}
