import { AfterViewInit, Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { MatDialogModule } from '@angular/material/dialog';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatExpansionModule,MatExpansionPanel  } from '@angular/material/expansion';

@Component({
  selector: 'app-cs-dialog',
  standalone: true,
  imports: [MatDialogModule,MatExpansionModule],
  templateUrl: './cs-dialog.component.html',
  styleUrl: './cs-dialog.component.css'
})
export class CsDialogComponent {
  constructor(@Inject(MAT_DIALOG_DATA) public data: any) {}
  @ViewChild('calculator') calculatorPanel!: MatExpansionPanel;
  @ViewChild('calculatorPanelRef', { read: ElementRef }) calculatorPanelRef!: ElementRef;
  ngAfterViewInit() {
    if (this.data?.panelToOpen === 'calculator') {
      setTimeout(() => {
        this.calculatorPanel.open();
        setTimeout(() => {
          this.calculatorPanelRef.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }, 100);
      }, 100);
    }
  }
}
